import React, { useState } from "react";
import * as XLSX from "xlsx";
import axios from "axios";

const ExcelUploader = () => {
  const [excelData, setExcelData] = useState([]);
  const [error, setError] = useState(""); // State to store error messages
  const [sheetName, setSheetName] = useState(""); // To store the current sheet name

  // Validation function for the Excel data
  const validateData = (data) => {
    let isValid = true;
    let errorMessage = "";

    // Loop through the data and validate each cell
    for (let rowIndex = 0; rowIndex < data.length; rowIndex++) {
      for (let key in data[rowIndex]) {
        const cellValue = data[rowIndex][key];

        // Validation 1: Check if the cell has more than 35 characters
        if (cellValue && cellValue.length > 35) {
          isValid = false;
          errorMessage += `Sheet: ${sheetName} - Row ${rowIndex + 1}, Column ${key} has more than 35 characters.\n`;
        }

        // Validation 2: Check if the cell is null or undefined
        if (cellValue === null || cellValue === undefined || cellValue === "") {
          isValid = false;
          errorMessage += `Sheet: ${sheetName} - Row ${rowIndex + 1}, Column ${key} has null or empty value.\n`;
        }
      }
    }

    setError(errorMessage);
    return isValid;
  };

  const handleFileUpload = (event) => {
    const file = event.target.files[0];
    const reader = new FileReader();

    reader.onload = (e) => {
      const data = new Uint8Array(e.target.result);
      const workbook = XLSX.read(data, { type: "array" });

      // Loop through all sheets and process each
      workbook.SheetNames.forEach((sheet, index) => {
        const sheetData = XLSX.utils.sheet_to_json(workbook.Sheets[sheet]);
        setSheetName(sheet);  // Update sheet name to track the current sheet

        setExcelData(sheetData);
        console.log(`Processing Sheet: ${sheet}`); // Log the sheet name for debugging

        // Validate the data for this sheet
        validateData(sheetData);
      });
    };

    reader.readAsArrayBuffer(file);
  };

  const uploadData = async () => {
    // First, validate the data
    const isValid = validateData(excelData);

    if (!isValid) {
      alert("There are validation errors. Please check the console for details.");
      console.log(error); // Log the errors to the console
      return;
    }

    try {
      const response = await axios.post("http://localhost:5000/upload-all", excelData);
      console.log("Uploaded all data:", response.data);
    } catch (error) {
      console.error("Error uploading data:", error);
    }
  };

  return (
    <div>
      <input type="file" accept=".xlsx, .xls" onChange={handleFileUpload} />
      <button onClick={uploadData} disabled={excelData.length === 0}>
        Upload to Backend
      </button>

      {error && (
        <div style={{ color: "red", marginTop: "10px" }}>
          <h3>Errors:</h3>
          <pre>{error}</pre>
        </div>
      )}
    </div>
  );
};

export default ExcelUploader;
